const LOGLEVEL_LOG = 0;
export const LOGLEVEL_INFO = 1;
const LOGLEVEL_WARN = 2;
const LOGLEVEL_ERROR = 3;
const LOGLEVEL_NON_LOGGING = 4; // æ æ¥å¿è®°å½çº§å«ï¼sdkå°ä¸æå°ä»»ä½æ¥å¿
const MAX_LOG_LENGTH = 1000;
let globalLevel = LOGLEVEL_LOG;
// æåä½¿ç¨ wx.getLogManagerï¼æ²¡åç°å®è½èµ·å°ä»ä¹ä½ç¨
const bCanIUseWxLog = false;
const timerMap = new Map();
const LOG_PRE_TAG = '%cCallEngine%c%s';
const LOG_PRE_TAG_STYLE = 'padding: 1px 4px;border-radius: 3px;color: #fff;background: #444;';
const LOG_STYLE = 'display: inline';
// ç§»å¨ç«¯å headless chrome ä¸æå° "TRTC" log tag
