/**
 * å¯¹é½æ¯«ç§å­ç¬¦ä¸²
 * @param {*} ms æ¯«ç§
 * @returns {String} å¯¹é½åçæ¯«ç§æ¶é´å­ç¬¦ä¸²
 */
function padMs(ms) {
  const len = ms.toString().length;
  let ret;
  switch (len) {
    case 1:
      ret = `00${ms}`;
      break;
    case 2:
      ret = `0${ms}`;
      break;
    default:
      ret = ms;
      break;
  }

  return ret;
}

/**
 * logåç¼
 * @returns {String} æ¥å¿åç¼
 */
function getPrefix() {
  const date = new Date();
  return `[${date.toLocaleTimeString('en-US', { hour12: false })}.${padMs(date.getMilliseconds())}]`;
}

