const logger = {
  _data: [],
  _length: 0,
  _visible: false,

  // å°å½æ°åæ°æ¼æå­ç¬¦ä¸²
  arguments2String(type, args) {
    let s;
    if (args.length === 1) {
      s = `${getPrefix()} <${type}> ${args[0]}`;
    } else {
      s = `${getPrefix()} <${type}> `;
      for (let i = 0, { length } = args; i < length; i++) {
        if (isArrayOrObject(args[i])) {
          if (isInstanceOfError(args[i])) {
            s += stringifyError(args[i]);
          } else {
            s += JSON.stringify(args[i]);
          }
        } else {
          s += args[i];
        }
        s += ' ';
      }
    }
    return s;
  },

  /**
   * æå°è°è¯æ¥å¿
   */
  debug() {
    if (globalLevel <= LOGLEVEL_DEBUG) {
      // å¯¹åæ°ä½¿ç¨sliceä¼é»æ­¢æäºJavaScriptå¼æä¸­çä¼å (æ¯å¦ V8 - æ´å¤ä¿¡æ¯)
      // see:https://github.com/petkaantonov/bluebird/wiki/Optimization-killers#3-managing-arguments
      const s = this.arguments2String('DEBUG', arguments);
      logger.record(s, 'debug');
      if (PRINT_LOG_TAG) {
        console.debug(LOG_PRE_TAG, LOG_PRE_TAG_STYLE, LOG_STYLE, s);
      } else {
        console.debug(s);
      }
      if (bCanIUseWxLog) {
        wx.getLogManager().debug(s);
      }
    }
  },

  /**
   * æå°æ®éæ¥å¿
   */
  log() {
    if (globalLevel <= LOGLEVEL_LOG) {
      const s = this.arguments2String('LOG', arguments);
      logger.record(s, 'log');
      if (PRINT_LOG_TAG) {
        console.log(LOG_PRE_TAG, LOG_PRE_TAG_STYLE, LOG_STYLE, s);
      } else {
        console.log(s);
      }
      if (bCanIUseWxLog) {
        wx.getLogManager().log(s);
      }
    }
  },

  /**
   * æå°releaseæ¥å¿
   */
  info() {
    if (globalLevel <= LOGLEVEL_INFO) {
      const s = this.arguments2String('INFO', arguments);
      logger.record(s, 'info');
      if (PRINT_LOG_TAG) {
        console.info(LOG_PRE_TAG, LOG_PRE_TAG_STYLE, LOG_STYLE, s);
      } else {
        console.info(s);
      }
      if (bCanIUseWxLog) {
        wx.getLogManager().info(s);
      }
    }
  },

  /**
   * æå°åè­¦æ¥å¿
   */
  warn() {
    if (globalLevel <= LOGLEVEL_WARN) {
      const s = this.arguments2String('WARN', arguments);
      logger.record(s, 'warn');
      if (PRINT_LOG_TAG) {
        console.warn(LOG_PRE_TAG, LOG_PRE_TAG_STYLE, LOG_STYLE, s);
      } else {
        console.warn(s);
      }
      if (bCanIUseWxLog) {
        wx.getLogManager().warn(s);
      }
    }
  },

  /**
   * æå°éè¯¯æ¥å¿
   */
  error() {
    if (globalLevel <= LOGLEVEL_ERROR) {
      const s = this.arguments2String('ERROR', arguments);
      logger.record(s, 'error');
      if (PRINT_LOG_TAG) {
        console.error(LOG_PRE_TAG, LOG_PRE_TAG_STYLE, LOG_STYLE, s);
      } else {
        console.error(s);
      }
      // å¾®ä¿¡åä¸äºerroræ¥å¿ï¼å°±ç¨warnä»£æ¿äº
      if (bCanIUseWxLog) {
        wx.getLogManager().warn(s);
      }
    }
  },

  time(label) {
    timerMap.set(label, TimeUtil.now());
  },

  timeEnd(label) {
    if (timerMap.has(label)) {
      const cost = TimeUtil.now() - timerMap.get(label);
      timerMap.delete(label);
      return cost;
    }
    console.warn(`æªæ¾å°å¯¹åºlabel: ${label}, è¯·å¨è°ç¨ logger.timeEnd åï¼è°ç¨ logger.time`);
    return 0;
  },

  setLevel(newLevel) {
    if (newLevel < LOGLEVEL_NON_LOGGING) {
      console.log(`${getPrefix()}set level from ${globalLevel} to ${newLevel}`);
    }
    globalLevel = newLevel;
  },

  getLevel() {
    return globalLevel;
  },

  record(s, type) {
    if (bCanIUseWxLog) {
      // å°ç¨åºç¯å¢ä¸å¨åå­ç¼å­æ¥å¿
      return;
    }

    if (logger._length === MAX_LOG_LENGTH + 100) {
      logger._data.splice(0, 100);
      logger._length = MAX_LOG_LENGTH;
    }
    logger._length++;
    logger._data.push(`${s} [${type}] \n`);
  },

  getLog() {
    return logger._data;
  },
};

export default logger;
